<script>
  import { _ } from 'svelte-intl';
  let firstContent = '';
  let secondContent = '';
  const content = [$_('about.first'), $_('about.second')]
  const typeWriter = () => {
    if(firstContent.length<content[0].length){
      firstContent = content[0].substr(0, firstContent.length + 1)
      setTimeout(typeWriter, 50)
    }else if(secondContent.length<content[1].length){
      secondContent = content[1].substr(0, secondContent.length + 1)
      setTimeout(typeWriter, 50)
    }
  }
  typeWriter()
</script>
<div class="container">
  <div class="nes-container with-title">
    <h2 class="title">{$_('about.title')}</h2>
    <p>{firstContent}</p>
    <p>{secondContent}</p>
  </div>
</div>